/* Vitals - CSS Grid */

.container-v {
  display: grid;
  justify-content: center;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows:
    3fr 1fr 4fr 1fr 1fr 1fr 1fr
    1fr 1fr 1fr 1fr 1fr;
  grid-column-gap: 5px;
  grid-row-gap: 2px;
  border: 2px solid black;
}

.item1 {
  background: #343a40;
  grid-column-start: 1;
  grid-column-end: 7;
  text-align: center;
  color: #00ffff;
  grid-row-start: 1;
  grid-row-end: 3;
  padding-top: 1px;
  padding-bottom: 1px;
  font-size: 35px;
}

.item2 {
  background: #00ffff;
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 3;
  grid-row-end: 5;
  text-align: center;
  padding-top: 7px;
}

